.card{
    /* height: 5rem; */
    width: 100%;
    margin-bottom: 5%;
    border-radius: 2%;
    background-color: whitesmoke;
    text-align: center ;
    /* border: solid 2px rgba(49, 48, 47,.5); */
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.black-title, .red-title, .orange-title, .green-title, .blue-title, .orange-title{
   /*border-bottom: solid 1px rgba(0, 0, 0,.5);*/
}
.black-title,.black-content{
    /* font-size: 15px; */
    /* font-weight: bold; */
    color: rgb(0, 0, 0);
    font-weight: 700;
    font-size: 18px;
    /* padding-top: 8px; */
}
.red-title,.red-content{
    /* font-size: 15px; */
    /* font-weight: bold; */
    color: rgb(197, 23, 23);
    font-weight: 700;
    font-size: 25px;
    line-height: 36px;
    /* padding-top:15px; */
}
.blue-title,.blue-content{
    font-size: 15px;
    font-weight: bold;
    color: rgb(35, 51, 194);
}
.green-title,.green-content{
    font-weight: bold;
    font-size: 15px;
    color: rgb(44, 100, 6);
}
.orange-title,.orange-content{
    font-size: 15px;
    font-weight: bold;
    color: rgb(201, 93, 22);
}
.card-updated{
    font-size: 12px;
    line-height: 20px;
    padding-bottom: 0px;
    padding-top: 8px;
    min-height: 28px;
    color: #4d4d4d;
}